.item__blog__container{
    width: 100%;
    margin-top: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    /* box-shadow: rgb(245, 122, 143,1) 0px 6px 12px -2px, rgba(245, 122, 143,1) 0px 3px 7px -3px; */
    box-shadow: rgb(245 213 219) 0px 6px 12px -2px, rgb(245 122 143) 0px 3px 7px -3px;;
    padding: 10px 10px;
    border-radius: 10px;
}
.blog__title{
    padding: 20px 20px;
}
.blog__body{
    width: 100%;
}
.blog__body-image{
    width: 100%;
    background-size: cover;
    background-repeat:no-repeat ;
    border-radius: 5px;
}
.blog__footer{
    display: flex;
    justify-content: space-between;
    padding: 10px 50px;
    border-bottom: 1px solid pink;
}
.blog__footer-like-icon{
    font-size: 2rem;
    fill: rgb(240, 88, 113);    
}
.blog__title-personal{
    display: flex;
    margin-bottom: 10px;
}
.blog__title-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-size: contain;
    border: 2px solid pink;
    padding: 2px 2px;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;

}
/* .blog__title-name {
    font-size: 1.2rem;
    margin-left: 20px;
} */
.blog__title-container-name{
    margin-left: 10px;
}

.see-more-comment{
    text-align: center;
    padding: 10px 0px ;
}
.blog__footer-listComment{
    background-color: #fffbf4;
    padding: 10px 10px;
}

.blog__footer-createComment{
    border-top: 1px solid pink;
    display: flex;
    align-items: center;
    padding: 10px 10px;
}
.blog__footer-control-input{
    padding: 0 20px;
    width: 100%;
}
input.blog__footer-wirte {
    width: 100%;
    padding: 10px 20px;
    border-radius: 13px;
    outline: none;
    border: 1px solid pink;
}

.write__blog-input--hover{
    background-color: rgb(255, 244, 246);;
}
.write__blog-control:hover .write__blog-input--hover {
    background: #ffd3da;
}